* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
  font-family: "Sono", sans-serif;
}

body {
  background-color: #292524;
  color: #fafaf9;
  padding: 4.8rem 6.4rem 0rem;
}

/*********** LAYOUTS*/
#root {
  max-width: 140rem;
  margin: 0 auto;
}

.header {
  margin-bottom: 4rem;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 4.8rem;
}

section {
  padding-bottom: 4rem;
}

/********* ELEMENTS */
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

h1 {
  margin-top: 1rem;
  line-height: 1;
  font-size: 4.2rem;
  text-transform: uppercase;
  font-family: "Coiny", sans-serif;
}

.logo-img {
  height: 6.4rem;
  width: 6.4rem;
}

.category {
  margin-bottom: 1.6rem;
}

.fact {
  background-color: #44403c;
  font-size: 2rem;
  line-height: 1.5;
  font-family: sans-serif;
  margin-bottom: 1.6rem;
  padding: 1.6rem 2.4rem;
  letter-spacing: 1px;
  border-radius: 1rem;

  display: flex;
  align-items: center;
  gap: 2.4rem;
}

.tag {
  text-transform: uppercase;
  font-size: 1.4rem;
  font-family: "Coiny", sans-serif;
  padding: 0.5rem 1rem 0 1rem;
  border-radius: 1rem;
}

ul {
  list-style: none;
}

.source:link,
.source:visited {
  color: #a8a29e;
  text-decoration: none;
  margin-left: 2rem;
  transition: all 0.1s ease;
}

.source:hover,
.source:active {
  color: #3b82f6;
}

.btn {
  font-family: "Coiny", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 1.8rem;
  padding: 1.6rem 0 1.3rem 0;
  color: inherit;
  cursor: pointer;
  transition: all 0.1s ease;
  border: none;
  border-radius: 1rem;
  background-image: linear-gradient(-60deg, #ff5858 0%, #f09819 100%);
}

.btn:hover {
  transform: scale(1.05);
}

.btn-large {
  font-size: 2rem;
  padding: 2rem 3.2rem 1.7rem 3.2rem;
}

.btn-all {
  margin-bottom: 1.6rem;
  width: 100%;
}

.btn-cat {
  width: 100%;
  background-image: none;
}

.vote-buttons {
  margin-left: auto;

  display: flex;
  flex-shrink: 0;
  gap: 0.8rem;
}

.vote-buttons button {
  background-color: #57534e;
  border: none;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  border-radius: 1rem;
  transition: all 0.1s ease;
  font-size: 1.8rem;
  padding: 0.6rem 1.2rem;

  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.vote-buttons button:hover {
  cursor: pointer;
  background-color: #78716c;
}

.vote-buttons button:active {
  background-color: #a8a29e;
}

.vote-buttons button:disabled {
  background-color: #44403c;
}

.fact-form {
  background-color: #44403c;
  margin-bottom: 4rem;
  padding: 1.6rem 3.2rem;
  border-radius: 1rem;

  display: flex;
  align-items: center;
  gap: 1.6rem;
}

.fact-form input,
.fact-form select {
  width: 22rem;
  background-color: #78716c;
  border: none;
  border-radius: 1rem;
  padding: 1.8rem;
  color: inherit;
  font-family: inherit;
}

.fact-form input::placeholder {
  color: #a8a29e;
}

.fact-form input:first-child {
  flex-grow: 1;
}

.fact-form span {
  font-size: 1.8rem;
  font-weight: 600;
  margin-right: 1.8rem;
}

/* below 1200px */
@media (max-width: 75em) {
  .fact {
    flex-direction: column;
    align-items: flex-end;
    gap: 1.2rem;
  }
}

/* below 1000px */
@media (max-width: 62.5em) {
  body {
    /* padding: 3.2rem 4.8rem; */
  }

  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }

  .fact-form input,
  .fact-form select {
    width: 100%;
  }
}

/* below 900px */
@media (max-width: 56.25em) {
  .header {
    flex-direction: column;
    gap: 3rem;
  }

  .header .btn-large {
    width: 100%;
  }

  .main {
    grid-template-columns: 1fr;
  }

  h1 {
    font-size: 3rem;
  }

  .btn-large {
    font-size: 1.6rem;
  }
}

.hidden {
  display: none;
}

.message {
  text-align: center;
  text-transform: uppercase;
  font-size: 2.4rem;
}

.disputed {
  color: #ef4444;
  font-weight: 600;
  margin-right: 1rem;
}
